/* Full-Screen Background */
body, html {
  height: 100%;
  margin: 0;
  background: linear-gradient(to right, #1e3c72, #2a5298);
  color: #fff;
}

/* Centered Layout */
.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

/* Stylish Search Bar */
.search-bar {
  width: 50%;
  padding: 15px 25px;
  border-radius: 50px;
  border: none;
  font-size: 18px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.search-bar:focus {
  outline: none;
  transform: scale(1.05);
}

/* Suggestion Buttons */
.suggestion-list {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.suggestion-item {
  background-color: #fff;
  color: #1e3c72;
  padding: 8px 15px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
}

.suggestion-item:hover {
  background-color: #1e3c72;
  color: #fff;
  transform: scale(1.1);
}

/* Horizontal Video List */
.video-list {
  display: flex;
  flex-wrap: wrap;  /* Allows wrapping to the next line if needed */
  justify-content: center;  /* Centers videos */
  gap: 20px;  /* Space between videos */
  padding: 20px;
  margin-top: 20px;
}

/* Individual Video Items */
.video-item {
  width: 300px;  /* Adjusts video thumbnail size */
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.video-item:hover {
  transform: scale(1.05);  /* Slight zoom on hover */
}

/* Video Thumbnails */
.video-item img {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

/* Video Title */
.video-title {
  font-size: 16px;
  margin-top: 8px;
  text-align: center;
}
